import React from "react";
import "./button.css";
import { MdModeEditOutline } from "react-icons/md";

interface ButtonProps {

  /**
   * React className 설정
   */
  className?: string[];

  /**
   * 버튼 내부 내용 설정
   */
  label?: string;
  /**
   * 클릭 핸들러
   */
  onClick?: () => void;

  /**
   * style 설정 
   */
  style?: object;

}

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  label,
  className,
  style,
  onClick,
}: ButtonProps) => (
  <button
    type="button"
    style={style}
    className={`button_default ${className.join(" ")}`}
    onClick={onClick}
  >
    {label}
  </button>
);

interface IconButtonProps {
  /**
   * 아이콘 종류를 설정
   */
  type: string;
}


export const IconButton = ({
  type
}: IconButtonProps) => (
  <MdModeEditOutline type={type}/>
)
/**
 * react/require-default-props
 */
Button.defaultProps = {
  className: [],
  style: { },
  onClick: () => { },
  label: 'this is button'
};

export default Button;
